document.addEventListener("DOMContentLoaded", () => {
    const countdownEl = document.querySelector('[data-countdown]');
    if (countdownEl) {
        const storage = window.localStorage;
        if (!storage.getItem('countdown') || Date.parse(new Date()) > Date.parse(storage.getItem('countdown'))) {
            const currentDate = new Date(),
                targetDate = new Date(
                    currentDate.getFullYear(),
                    currentDate.getMonth(),
                    currentDate.getDate() + Number(countdownEl.dataset.countdown),
                    currentDate.getHours(),
                    currentDate.getMinutes(),
                    currentDate.getSeconds()
                );
            storage.setItem('countdown', targetDate);
        }

        setCountdown();
        setInterval(() => {
            setCountdown();
        }, 1000);

        function setCountdown () {
            const countdownDate = window.countdown(Date.parse(storage.getItem('countdown'))),
                days = Number(countdownDate.days),
                hours = Number(countdownDate.hours),
                minutes = Number(countdownDate.minutes),
                seconds = Number(countdownDate.seconds);

            const countdownDays = countdownEl.querySelector('[data-countdown-value=days]'),
                countdownHours = countdownEl.querySelector('[data-countdown-value=hours]'),
                countdownMinutes = countdownEl.querySelector('[data-countdown-value=minutes]'),
                countdownSeconds = countdownEl.querySelector('[data-countdown-value=seconds]');

            countdownDays.querySelector('.count').innerHTML =
                `<span>${Math.floor(days / 10)}</span><span>${Math.floor(days % 10)}</span>`;
            countdownHours.querySelector('.count').innerHTML =
                `<span>${Math.floor(hours / 10)}</span><span>${Math.floor(hours % 10)}</span>`;
            countdownMinutes.querySelector('.count').innerHTML =
                `<span>${Math.floor(minutes / 10)}</span><span>${Math.floor(minutes % 10)}</span>`;
            countdownSeconds.querySelector('.count').innerHTML =
                `<span>${Math.floor(seconds / 10)}</span><span>${Math.floor(seconds % 10)}</span>`;

            countdownDays.querySelector('.val').innerHTML =
                window.pluralFormat(days, 'день', 'дня', 'дней');
            countdownHours.querySelector('.val').innerHTML =
                window.pluralFormat(hours, 'час', 'часа', 'часов');
            countdownMinutes.querySelector('.val').innerHTML =
                window.pluralFormat(minutes, 'минута', 'минуты', 'минут');
            countdownSeconds.querySelector('.val').innerHTML =
                window.pluralFormat(seconds, 'секунда', 'секунды', 'секунд');
        }
    }
})